import React, { useEffect} from 'react'
import { useLocation } from 'react-router-dom'
import ReactPlayer from 'react-player'
import { Loading } from './'

import { useResultContext } from '../contexts'

const Results = () => {
  const { results, isLoading, getResults, searchTerm } = useResultContext()
  const location = useLocation()
  useEffect(() => {
    if(searchTerm){
      if(location.pathname === '/videos'){
        getResults(`/search/q=${searchTerm}&num=40&tbm=vid`)
      } else{
        getResults(`${location.pathname}/q=${searchTerm}&num=40`)
      }
    }
  }, [searchTerm, location.pathname])
  console.log(results)
  if(isLoading) return <Loading />

  switch (location.pathname) {
    case '/search':
      return (
        <div className='flex flex-wrap justify-between space-y-6 sm:px-20'>
          {results?.results?.map(({link, title}, index) => (
            <div key={index} className='md:w-2/5 w-full'>
              <a href={link} target='_blank' rel='noopener noreferrer'>
                <p className="text-sm">
                  {link.length > 30 ? `${link.substring(0, 30)}...` : link}
                </p>
                <p className="text-lg hover:underline dark:text-blue-300 text-blue-700">
                  {title}
                </p>
              </a>
            </div>
          ))}
        </div>
      )
    case '/images':
      return (
        <div className='flex flex-wrap justify-center items-center'>
          {results?.image_results?.map(({image: {src}, link: {title, href}}, index) => (
            <a href={href} className="sm:p-3 p-5" key={index} target='_blank' rel='noreferrer'>
              <img src={src} alt={title} className='w-full' loading='lazy' />
              <p className="w-36 break-words text-sm mt-2">
                {title}
              </p>
            </a>
          ))}
        </div>
      )
    case '/videos':
      return (
        <div className="flex flex-wrap">
          {results?.results?.map(({link, title, additional_links}, index) => (
            <div key={index} className="p-2">
              { additional_links?.[0].href && <ReactPlayer url={additional_links?.[0].href} controls width='355px' height='200px' />}
            </div>
          ))}
        </div>
      )
    case '/news':
      return (
        <div className='flex flex-wrap justify-between space-y-6 sm:px-20 items-center'>
          {results?.news?.map(({links, id, source, title}) => (
            <div key={id} className='md:w-2/5 w-full'>
              <a href={links?.[0].href} target='_blank' rel='noopener noreferrer' className='hover:underline'>
                <p className="text-lg dark:text-blue-300 text-blue-700">
                  {title}
                </p>
              </a>
                <div className="flex gap-4">
                  <a href={source?.href} target='_blank' rel='noreferrer'>
                    {source?.href}
                  </a>
                </div>
            </div>
          ))}
        </div>
      )
    default:
      return 'no results'
  }
}

export default Results