import React from 'react'
import { Link } from 'react-router-dom'
import { Search  } from './'

const Navbar = ({ darkTheme, setDarkTheme }) => {
  return (
    <div className='p-5 pd-0 flex flex-wrap sm:justify-between justify-center items-center border-b dark:border-gray-700 border-gray-200'> 
      <div className='flex justify-between items-center space-x-5 w-screen'>
        <Link to='/'>
          <p className="text-2xl bg-blue-300 px-2 py-1 font-bold text-white rounded dark:bg-gray-500 dark:text-gray-900">
            Search
          </p>
        </Link>
        <button type='button' onClick={() => setDarkTheme(!darkTheme)} className='text-xl dark:bg-gray-900 dark:text-white dark:border dark:hover:bg-gray-600 bg-gray-50 border rounded-full px-2 py-1'>
          {!darkTheme ? 'Light' : 'Dark'}
        </button>
      </div>
      <Search />
    </div>
  )
}

export default Navbar